html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.4rem;
}

p {
  font-size: 1.3rem;
  color: #000;
}

.container {
  max-width: 80rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 3rem;
  font-weight: 900;
}

h2 {
  font-size: 2.6rem;
}

h3 {
  font-size: 2rem;
}

.header {
  background-color: #4CB8B3;
}
.header .content-header {
  padding: 1.75rem 0;
}
@media only screen and (min-width: 768px) {
  .header .content-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header h1 {
  text-align: center;
  color: #FFF;
  margin-bottom: 0.5rem;
}
@media only screen and (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}
.header.fixed {
  position: fixed;
  width: 100%;
  box-shadow: 0 0.2rem 0.3rem #444;
}

.main-nav {
  display: flex;
  gap: 2rem;
  justify-content: center;
}
.main-nav a {
  color: #FFF;
  font-size: 1.8rem;
}
.main-nav a:hover {
  color: #FEF46D;
}

.video {
  position: relative;
  max-height: 52rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, #F53756, #FEF46D);
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+67,ffffff+100&0.8+0,0.5+100 */
  background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#cc891c1e", endColorstr="#80ffffff",GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
}
.video video {
  display: block;
  width: 100%;
}

.content-video {
  display: flex;
  justify-content: start;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1;
}
.content-video h2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #FFF;
  font-size: 4.5rem;
}
.content-video h2 span {
  background-color: #F53756;
  font-size: 1.8rem;
  padding: 0.6rem 1.2rem;
}

.info-festival {
  padding: 1.75rem 0;
}
@media only screen and (min-width: 768px) {
  .info-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3.5rem;
    column-gap: 3.5rem;
    padding: 5.25rem 0;
  }
}
.info-festival .image {
  padding: 0 1rem 2rem;
}

.content-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.content-festival h2 {
  font-size: 2.3rem;
}
.content-festival .date {
  color: #F53756;
  font-weight: 700;
}
.content-festival .text {
  color: black;
}

.lineup {
  background-color: #F53756;
  padding: 3.5rem 0;
}
.lineup h3 {
  font-weight: 900;
  color: #FFF;
  text-align: center;
}
.lineup .day {
  text-align: center;
  text-transform: uppercase;
  color: #FFF;
  font-weight: 700;
  margin: 3.5rem 0;
}

@media only screen and (min-width: 768px) {
  .stage-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
.stage-container .bg-yellow {
  background-color: #FEF46D;
}
.stage-container .bg-green {
  background-color: #4CB8B3;
}

.stage {
  margin-bottom: 2rem;
}
.stage .stage-title {
  text-align: center;
  font-size: 1.4rem;
  font-weight: 300;
}
.stage .stage-title:after {
  content: "";
  width: 20rem;
  height: 10rem;
  display: block;
  margin: 1rem auto 0 auto;
  background-size: 12rem;
  background-repeat: no-repeat;
  background-position: center center;
}
.stage .calendary {
  padding: 0;
}
.stage .calendary li {
  list-style: none;
  padding: 1.75rem 3.5rem;
  font-size: 1.3rem;
}
.stage .calendary li span {
  font-weight: 700;
}

.edm .stage-title:after {
  background-image: url("../img/icono_edm.webp");
}

.rock .stage-title:after {
  background-image: url("../img/icono_rock.webp");
}

.bg-yellow .calendary li:nth-child(odd) {
  background-color: #fef254;
}

.bg-green .calendary li:nth-child(odd) {
  background-color: #43a8a3;
}

.galery h3 {
  text-align: center;
  padding: 3.5rem;
  font-weight: 900;
}

.content-galery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.75rem;
  padding-left: 0;
  padding-bottom: 5rem;
}
.content-galery li {
  list-style: none;
}
.content-galery li img {
  display: block;
  margin: auto;
}
@media only screen and (min-width: 480px) {
  .content-galery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1.75rem;
  }
}
@media only screen and (min-width: 768px) {
  .content-galery {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 1.75rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5rem;
}
.overlay IMG {
  width: 70rem;
}

.btn-close {
  display: grid;
  place-items: center;
  background-color: #FFF;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  font-weight: 900;
  font-size: 1.5rem;
  text-align: center;
}
.btn-close:hover {
  cursor: pointer;
}

body.hidden-body {
  overflow: hidden;
}

.bg-green {
  background-color: #4CB8B3;
}

.bg-red {
  background-color: #F53756;
}

.bg-white {
  background-color: #FFF;
}

.bg-purple {
  background-color: #752F97;
}

.tickets {
  padding: 3.5rem 0;
}
.tickets h3 {
  text-align: center;
  color: #FFF;
  font-weight: 900;
}

.tickets-content {
  padding: 1.75rem 0;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1.75rem;
  justify-items: center;
}
@media only screen and (min-width: 768px) {
  .tickets-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.75rem;
  }
}
.tickets-content .basic, .tickets-content .premium {
  width: 100%;
}
.tickets-content .basic p, .tickets-content .premium p {
  text-align: center;
  color: #FFF;
  font-weight: 300;
  font-size: 2rem;
}

.table {
  width: 100%;
  border-spacing: 0;
}
.table thead, .table tfoot {
  text-align: center;
}
.table th {
  color: #FFF;
}
.table td {
  border-bottom: 0.5px solid #e1e1e1;
  font-size: 1.35rem;
}
.table tr:last-of-type td {
  border: none;
}
.table th, .table td {
  padding: 0.875rem;
}
.table tfoot td {
  color: #FFF;
  font-size: 2rem;
}

.bg-red {
  background-color: #F53756;
}

footer {
  padding: 1.1666666667rem;
  text-align: center;
}
footer p {
  margin: 0;
  color: #FFF;
}